.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  box-shadow: 2px 2px 50px $very-light-gray;
  overflow: scroll;
  transform: translate(-100%);
  transition: all 0.5s ease;
  opacity: 0;
  background: white;

  h2 {
    padding: 2rem;
  }

  .library-song {
    display: flex;
    align-items: center;
    padding: 1rem 2rem;
    cursor: pointer;
    transition: background 0.5s ease;

    &:hover {
      background: $light-gray;
    }

    img {
      width: 30%;
      border-radius: 50%;
    }

    .song-description {
      padding-left: 1rem;

      h3 {
        font-size: 1rem;
      }

      h4 {
        font-size: 0.7rem;
      }
    }
  }
}

.selected {
  background: $light-gray;
}

.active-library {
  transform: translate(0%);
  opacity: 1;
}

// scrollbar styling
* {
  scrollbar-width: thin;
  scrollbar-color: $off-gray transparent;
}

*::-webkit-scrollbar {
  width: 5px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: $off-gray;
  border-radius: 20px;
  border: transparent;
}

@media screen and (max-width: $media-query-breakpoint) {
  .library {
    width: 100%;
  }
}
